import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CustomerDietViewComponent } from './customer-diet-view.component';

const routes: Routes = [
  {
    path:'',
    component:CustomerDietViewComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CustomerDietViewRoutingModule { }
