import { Component, OnInit } from '@angular/core';
import { CommonVariables } from 'src/app/shared/commmon';
import { HttpClient } from '@angular/common/http';
import { CommonResponse } from 'src/app/shared/commmon/ServiceClass';
import { map } from 'rxjs/operators';
import { CustomerDietViewService } from './customer-diet-view.service';
import { connectableObservableDescriptor } from 'rxjs/internal/observable/ConnectableObservable';
import { Router } from '@angular/router';
import { ViewMediaComponent } from '../view-media/view-media.component';
import { MatDialog } from '@angular/material';

@Component({
  selector: 'app-customer-diet-view',
  templateUrl: './customer-diet-view.component.html',
  styleUrls: ['./customer-diet-view.component.scss']
})
export class CustomerDietViewComponent implements OnInit {
  index = 0;
  DishData = [];
  DietDates = [];
  MealTypes = [];
  Dishes;
  DishVideos = [];

  constructor(private service: CustomerDietViewService, private router: Router, private dialog: MatDialog,
    private util: CommonVariables ) {
    this.GetCustomerDietData();
  }

  ngOnInit() {
  }
  // add the index
  AddIndex(value) {
    this.index = this.index + value;
    localStorage.setItem("LastUsedIndex",this.index.toString());
  }
  // get customer dish data
  GetCustomerDietData() {
    this.service.GetCustomerDietData().subscribe(Response => {
      if (Response.Status === true) {
        const data: DietDishData = Response.Data;
        this.GenerateDates(data.Food);
        this.Dishes = data.Food;
        this.MealTypes = data.MealTypes;
      }
    });
    let index=parseInt(localStorage.getItem("LastUsedIndex"));
    if(this.DietDates.length<=index&&index){
      this.index=index;
    }
  }
  // generate diet dates
  GenerateDates(DishData) {
    DishData.forEach(element => {
      this.DietDates.push(element.Date);
    });
    this.DietDates = this.DietDates.filter(function(elem, index, self) {
      return index === self.indexOf(elem);
  });

  }
  // filter dish items
  FilterDishItems(MealTypeID) {
    const Dishes = [];
    this.Dishes.forEach(element => {
      if (element.Date === this.DietDates[this.index]) {
        Dishes.push(element.Dishes);
      }
    });
    const FinalArray = [];
    Dishes.forEach(element => {
      let Foods = '';
      element.forEach(element => {
        if (element.MealTypeId === MealTypeID) {
          Foods = Foods + ',' + element.Quantity + ' ' + element.Title;
        }
      });
      if (Foods.length > 0) {
        Foods = Foods.substring(1, Foods.length);
      }
      if (Foods.length > 0) {
        FinalArray.push({Dishes: Foods});
      }
    });
    return FinalArray;
  }
  // filter dish which have recipe
  FilterDishWithRecipe(MealTypeID) {
    const Dishes = [];
    this.Dishes.forEach(element => {
      if (element.Date === this.DietDates[this.index]) {
        Dishes.push(element.Dishes);
      }
    });
    const FinalArray = [];
    Dishes.forEach(element => {
      const Foods = '';
      element.forEach(element => {
        if (element.MealTypeId === MealTypeID && element.Ingredients > 0 ) {
          FinalArray.push(element);
        }
      });
    });
    return FinalArray;
  }
  // show ingredients
  ShowIngredients(DishId) {
    this.router.navigate(['/cs/recipe/' + DishId]);
  }
  // filterDishVideos
  FilterDishVideos(MealTypeID) {
    const Dishes = [];
    this.Dishes.forEach(element => {
      if (element.Date === this.DietDates[this.index]) {
        Dishes.push(element.Dishes);
      }
    });
    const FinalArray = [];
    Dishes.forEach(element => {
      const Foods = '';
      element.forEach(element => {
        if (element.MealTypeId === MealTypeID && element.DishVideos.length > 0 ) {
          FinalArray.push(element.DishVideos[0]);
        }
      });
    });
    return FinalArray;
  }
  // show video
  OpenMedia(FileLocation, SourceType) {
      const FinalUrl = this.util.base_url + FileLocation;
      const dialogRef = this.dialog.open(ViewMediaComponent, {
      panelClass: 'MediaDialogBox',
      disableClose: true,
      data: {'MediaSource': FinalUrl, 'MediaType': SourceType}
      });
    // fires when dialogue closed
      dialogRef.afterClosed().subscribe(result => {
      //
      });
  }
  // get chef profile pic
  GetImageLocation(filename) {
    let FinalLocation = '';
    if (filename == null) {
      FinalLocation = 'url(/assets/images/recipe-placeholder.jpg)';
    } else {
      FinalLocation = 'url(' + this.util.base_url + filename + ')';
    }
    return FinalLocation;
  }
}
export interface DietDishData {
  MealTypes;
  Food;
}
export interface Dishes {
  Date;
  Dishes: [{
    CustomerDietId;
    MealTypeId;
    MealTypeName;
    DishId;
    Title;
    Quantity;
    Ingredients;
    DishVideos;
  }];

}
